import { apiGetNavList } from '@/apis/mine.js';
import { apiGetRecommendSongs } from '@/apis/index.js';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import songList from '@/components/songList.vue';

export default {
	components: {
		uniNavBar,
		songList
	},
	data() {
		return {
			navList: [
				{ name: '私人FM', imd: 1 },
				{ name: '最嗨电音', imd: 2 },
				{ name: 'ACG专区', imd: 3 },
				{ name: 'Sati空间', imd: 4 },
				{ name: '私藏推荐', imd: 5 },
				{ name: '因乐交友', imd: 6 },
				{ name: '亲子频道', imd: 7 },
				{ name: '古典专区', imd: 8 }
			],
			recommendSongs: []
		};
	},
	onLoad() {
		this.getNavList();
		this.getRecommendSongs();
	},
	methods: {
		// 获取导航
		getNavList() {
			const params = {
				id: 124
			};
			// apiGetNavList(params).then(res => {
			// 	this.navList = res.mock.navList;

			// 	const tempId = this.navList[0].id;
			// });
		},
		goUrl() {},
		goCloud() {
			uni.showToast({
				icon: 'none',
				title: '功能未开发'
			});
		},
		// 获取推荐歌单
		getRecommendSongs() {
			const params = {
				limit: 6
			};
			apiGetRecommendSongs(params).then(res => {
				// 格式化播放量数据
				const formatCount = data => {
					let tempNum = data;
					if (data > 100000) {
						tempNum = parseInt(data / 10000) + '万';
					}
					return tempNum;
				};
				this.recommendSongs = res.result;

				this.recommendSongs.forEach(item => {
					item.playCount = formatCount(item.playCount);
				});
			});
		}
	}
};
